import React from "react"
import PropTypes from "prop-types"
import { StaticQuery, graphql } from "gatsby"
import styled from "@emotion/styled"
import icon from "../images/icon.png"
import { FaLinkedin } from 'react-icons/fa';

const Container = styled.div`
  text-align: center;
`

const OuterContainer = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  height: 78vh;
`

const Description = styled.p`
  padding: 0;
  margin-bottom: 1rem;
  font-size: 1.4rem;
`

const Subtitle = styled.h2`
  display: flex;
  justify-content: center;
  padding: 0;
  margin-bottom: 1rem;
  font-size: 1.4rem;
`

const NameHeader = styled.h1`
  font-size: 3.5rem;
  margin-bottom: 0;
`

const Logo = styled.img`
  height: 200px;
  width: 200px;
  margin: 0;
`

const LinkedIn = styled.span`
  margin-left: 15px;
`

const LandingBio = () => (
  <StaticQuery
    query={graphql`
      query LandingSiteTitleQuery {
        site {
          siteMetadata {
            title
            subtitle
            description
          }
        }
      }
    `}
    render={data => (
      <OuterContainer>
        <Container>
          <Logo src={icon} alt={icon}></Logo>
          <NameHeader>{data.site.siteMetadata.title}</NameHeader>
          <Subtitle>{data.site.siteMetadata.subtitle}<LinkedIn><a href="https://www.linkedin.com/in/payam-mesgari-461990b2/"><FaLinkedin></FaLinkedin></a></LinkedIn></Subtitle>
          <Description>{data.site.siteMetadata.description}</Description>
        </Container>
      </OuterContainer>
    )}
  />
)

NameHeader.propTypes = {
  siteTitle: PropTypes.string,
  subtitle: PropTypes.string,
}

NameHeader.defaultProps = {
  siteTitle: ``,
  subtitle: ``,
}

export default LandingBio
